const shuffle = (arr) => {
  const arrLength = arr.length
  let i = arrLength
  let rndNum
  while (i--) {
    if (i !== (rndNum = Math.floor(Math.random() * arrLength))) {
      [arr[i], arr[rndNum]] = [arr[rndNum], arr[i]]
    }
  }
  return arr
}

export const getHomeSolider = (slidesData) => {
  return new Promise(resolve => {
    if (slidesData) {
      let slides = slidesData
      const slide = [slides[Math.floor(Math.random() * slides.length)]]
      slides = shuffle(slides.filter(() => Math.random() >= 0.5))
      if (slides.length === 0) {
        resolve(slide)
      }
      resolve(slides)
    }
    throw new Error('获取错误！')
  }).catch(err => {
    if (err) {
      console.log(err)
      return [{picUrl: require('assets/img/404.png')}]
    }
  }).then(res => {
    return new Promise(resolve => {
      setTimeout(() => {
        resolve(res)
      }, 400)
    })
  })
}

